:host {
  display: block;
  position: relative;
  --p-treeselect-background: var(--p-tree-background) !important;
}

.searchable-tree-container {
  width: 100%;
  position: relative;

  .search-input-container {
    position: relative;

    .p-progress-spinner {
      position: absolute;
      right: 48px;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
    }
  }

  .tree-dropdown {
    position: absolute;
    width: 100%;
    min-width: 250px;
    z-index: 100;
    max-height: 350px;
    overflow-y: auto;
    margin-top: 4px;
    cursor: default;
    background: var(--p-select-overlay-background);
    color: var(--p-select-overlay-color);
    border: 1px solid var(--p-select-overlay-border-color);
    border-radius: var(--p-select-overlay-border-radius);
    box-shadow: var(--p-select-overlay-shadow);
  }

  .search-results {
    margin-top: 0.25rem;

    li {
      border-radius: 4px;
    }
  }
}

:host ::ng-deep {
  .p-treeselect {
    width: 100%;

    .p-treeselect-label {
      padding: 0.5rem 0.75rem;
    }
  }

  .p-tree {
    border: none;
    padding: 0.25rem;
  }
}

.max-h-15rem {
  max-height: 15rem;
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
